@import 'mixins/crop';

.root {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    position: relative;
    cursor: pointer;
    user-select: none;

    &.toggle {
        .wrap {
            width: 36px;
            height: 20px;
            margin-right: 0;
            margin-left: 8px;
            flex-shrink: 0;

            &:only-child {
                margin-right: 0;
            }

            .toggler {
                border-radius: 12px;
                background-color: #999;
                width: 100%;
                height: 100%;
                position: relative;
                transition: background-color 0.3s ease;

                &::after {
                    content: '';
                    display: block;
                    height: calc(100% - 4px);
                    width: calc(50% - 2px);
                    border-radius: 100px;
                    background-color: #fff;
                    position: absolute;
                    top: 2px;
                    left: 2px;
                    transition: transform 0.3s ease;
                }
            }

            .input {
                &:checked + .toggler {
                    background-color: #3fb990;

                    &::after {
                        transform: translateX(100%);
                    }
                }

                &:disabled + .toggler {
                    background-color: #999;
                }
            }
        }

        &.builderInput {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: row-reverse;
            padding: 0;

            .wrap {
                flex-shrink: 0;
            }

            .label {
                width: calc(100% - 44px);

                > * {
                    display: block;
                    font-weight: 500;
                    font-size: 14px;
                    color: #333;
                    padding: 0 0 0 10px;
                    @include crop();
                }
            }

            .input {
                &:checked + .toggler {
                    background-color: #2979ff;
                }

                &:disabled + .toggler {
                    background-color: #999;
                }
            }
        }
    }
}

.checkMarkSquare {
    transition: fill 0.3s ease, stroke 0.3s ease;
}

.checkMark {
    transform: scale(0);
    transform-origin: 50% 50%;
    transition: transform 0.3s ease;
}

.checked .checkMark {
    transform: scale(1);
}

.input {
    position: absolute;
    opacity: 0;
    cursor: pointer;

    & ~ svg {
        transform: translateY(1px);

        .check {
            opacity: 0;
        }
    }

    &:checked .checkMark {
        transform: scale(1);
    }
}

.label {
    flex-grow: 1;
    line-height: 22px;
    font-size: 16px;
    font-weight: normal;
}

.wrap {
    line-height: 0;
    margin-right: 8px;
}

.disabled {
    pointer-events: none;
    opacity: 0.6;
}
