.root {
    .img {
        visibility: hidden;
        display: block;
        width: 100%;
    }

    &.fit {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        .img {
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
        }
    }

    .spinner {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0;
        width: 100%;
        height: 100%;
    }

    .visible {
        visibility: visible;
    }

    &.inline {
        .spinner {
            position: absolute;
        }
    }
}
