@import 'vars';

.container {
    position: relative;
    flex: 1;

    textarea {
        display: block;
        -webkit-appearance: none;
    }
}

.root {
    font-size: 16px;
    font-weight: normal;
    line-height: 24px;
    border: 1px solid #c7c7cc;
    border-radius: 4px;
    width: 100%;
    padding: 11px 16px;
    color: #2b303a;
    resize: none;
    transition: border-color 0.3s ease, background-color 0.3s ease;
    -webkit-overflow-scrolling: touch;

    &::-webkit-scrollbar {
        width: 0;
    }

    &::-webkit-scrollbar-track {
        background: rgba(255, 255, 255, 0);
    }

    &::-webkit-scrollbar-thumb {
        background: rgba(255, 255, 255, 0);
        transition: background 0.3s ease;
    }

    &:hover {
        border-color: #8e8e93;
    }

    &:focus {
        outline: 0;
        border-color: #007aff;

        + .counter {
            opacity: 1;
            transform: scale(1);
        }
    }

    &:disabled {
        opacity: 0.3;
    }

    &::placeholder {
        color: #8e8e93;
    }

    &.error {
        border-color: #dc5960;
    }

    &.padding-right {
        padding-right: 33px;
    }

    &.builderInput {
        background-color: #f7f7fa;
        color: #333;
        border-radius: 10px;
        border: none;
        padding: 8px 16px;

        &:focus,
        &:hover {
            background-color: #e5f6ff;
        }

        &.error {
            background-color: #fbeeef;
        }
    }
}

.counter {
    opacity: 0;
    transform: scale(0.5);
    display: block;
    font-size: 12px;
    line-height: 18px;
    position: absolute;
    bottom: 13px;
    right: 12px;
    transition: transform 0.15s ease, opacity 0.3s ease;
}

.normal {
    padding: 6px 26px 6px 16px;
    font-size: 14px;
    line-height: 20px;
    min-height: 52px;
    margin: 6px 0 0 0;
}
