@import 'vars';
@import 'mixins/crop';

.root {
    display: block;
    height: 100%;

    &.disabled {
        opacity: 0.4;
        filter: grayscale(1);
    }

    > .thumbnail {
        width: 100%;
        padding-top: 100%;
        position: relative;
        overflow: hidden;
        border-radius: 10px;

        > img {
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            transform: translate3d(0, 0, 0);
        }

        .emptyPlaceholder {
            width: 100%;
            height: 100%;
            background-color: #eee;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 10px;
        }
    }

    .meta {
        padding: 8px 6px 0 6px;
    }

    .title {
        display: block;
        margin: 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.4;
        //@include crop();
        word-break: break-word;
    }

    .price {
        display: block;
        font-weight: normal;
        font-size: 12px;
        line-height: 1.5;
        margin: 4px 0 0 0;
        @include crop();

        .discountPrice {
            margin: 0 8px 0 0;
        }

        .lineThrough {
            opacity: 0.5;
            text-decoration: line-through;
        }
    }
}
