.ordersList {
    margin: 0;
    padding: 0;
    list-style: none;

    > li {
        margin: 0 0 12px 0;

        > a {
            display: block;
            border-radius: 12px;
            padding: 16px;

            .orderHeader {
                width: 100%;
                display: flex;

                > div:first-of-type {
                    width: 100%;
                    flex-shrink: 1;
                    overflow: hidden;

                    > h2 {
                        width: 100%;
                        font-weight: 700;
                        font-size: 22px;
                        margin: 0;
                        line-height: 1;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        padding: 0 10px 0 0;
                    }

                    > div {
                        display: flex;
                        margin: 6px 0 0 0;

                        > span {
                            padding: 2px 10px;
                            border-radius: 100px;
                            font-size: 12px;
                            margin: 0 4px 0 0;
                        }
                    }

                    > span {
                        display: block;
                        font-size: 14px;
                        opacity: 0.6;
                    }
                }

                > div:last-of-type {
                    flex-shrink: 0;
                    text-align: right;
                    font-size: 12px;

                    > div:first-of-type {
                        margin: 0 0 4px 0;
                    }

                    > div > span {
                        display: block;
                        font-size: 14px;
                        opacity: 0.6;
                        line-height: 22px;
                    }
                }
            }

            .orderContent {
                display: flex;
                align-items: flex-end;
                margin: 12px 0 0 0;

                > div:first-of-type {
                    width: 100%;
                    flex-shrink: 1;
                    overflow: hidden;

                    > h3 {
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 1.2;
                        opacity: 0.6;
                        margin: 0;
                    }

                    > span {
                        display: block;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        padding: 0 10px 0 0;
                        line-height: 1;
                        margin: 5px 0 0 0;
                    }
                }

                > div:last-of-type {
                    flex-shrink: 0;
                    font-weight: 700;
                    font-size: 18px;
                    line-height: 1;
                }
            }
        }

        &:last-of-type {
            margin: 0;
        }
    }
}
