.root {
    height: 32px;
    cursor: pointer;
    display: inline-flex;
    margin: 0 auto;
    align-items: center;
    padding: 5px 24px;
    color: #2979ff;
    background-color: #f7f7fa;
    user-select: none;
    overflow: hidden;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    letter-spacing: 0.02px;
    position: relative;
    justify-content: center;

    @media all and (min-width: 1025px) {
        &:hover {
            box-shadow: 0 10px 10px -6px rgba(0, 0, 0, 0.24);
            transform: translateY(-2px);
        }
    }
}

.disabled {
    color: #888;
    pointer-events: none;
}
