.label-container {
    display: flex;

    > a {
        margin-left: auto;
        position: relative;

        &::before {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            background-color: #007aff;
            position: absolute;
            bottom: 8px;
            left: 0;
        }
    }
