@import 'vars';

.wrapper {
    position: relative;
}

.root {
    appearance: none;
    display: block;
    width: 100%;
    font-size: 16px;
    font-weight: normal;
    height: 42px;
    line-height: 20px;
    border: 1px solid #c7c7cc;
    border-radius: 4px;
    padding: 11px 32px 11px 16px;
    color: #333;
    transition: border-color 0.3s ease;
    cursor: pointer;

    &.builderInput {
        height: 38px;
        border: none;
        border-radius: 10px;
        background-color: $background-light;
        transition: background-color 0.3s ease;
        padding: 0 32px 0 16px;

        &:focus,
        &:hover {
            background-color: #e5f6ff;
        }
    }

    &:focus {
        outline: 0;
    }

    &::placeholder {
        color: #929499;
    }

    &.error {
        border-bottom-color: #ff3c3c;
    }

    &.normal {
        padding: 6px;
        height: 32px;
        font-size: 14px;
        line-height: 20px;
        background: none;

        &:hover {
            border-color: #8e8e93;
        }

        &:focus {
            border-color: #007aff;
        }

        option {
            color: #000;
        }
    }

    &.small {
        padding: 0 16px;
        height: 32px;
        font-size: 14px;
        line-height: 24px;

        &:hover {
            border-color: #8e8e93;
        }

        &:focus {
            border-color: #007aff;
        }

        option {
            color: #000;
        }
    }
}

.placeholder {
    color: #8e8e93;
}

.disabled {
    opacity: 0.6;
}

.arrowIcon {
    display: block;
    width: 8px;
    position: absolute;
    right: 15px;
    top: calc(50% - 3px);
    pointer-events: none;
}
