.orBar {
    margin: 16px 0;

    span {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 8px;
        gap: 8px;

        &:before,
        &:after {
            border-top: 1px solid #dfdfdf;
            content: '';
            width: 80%;
        }
    }
}

.choices {
    margin: 16px 0;
}

.choice {
    padding: 5px 0;

    .choiceWrapper {
        border-radius: 10px;
        height: 44px;
        display: flex;
        align-items: center;
        flex: 1;
        padding: 0 16px;
    }

    .choiceContent {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        > div {
            display: flex;
            flex-direction: row;
            align-items: center;
            column-gap: 5px;
        }
    }
}

.paymentButtons > * {
    margin: 0 0 16px 0;

    &:last-child {
        margin: 0;
    }
}
