@import 'vars';
@import 'mixins/crop';

.wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    opacity: 0;
    animation: opacity 0.3s ease forwards;
}

.root {
    color: #fff;
    width: 100%;
    height: 100%;
    overflow: auto;
    overflow: overlay;
    opacity: 0.3;
    transform: translateY(16px);
    transition: opacity 0.6s ease, transform 0.6s ease;
    padding: 16px 0 0 0;

    &.active {
        opacity: 1;
        transform: none;
    }
}

@media all and (min-width: 601px) {
    .root::-webkit-scrollbar {
        width: 4px;
    }

    .root::-webkit-scrollbar-track {
        background: #55555500;
    }

    .root::-webkit-scrollbar-thumb {
        background: #55555580;
        border-radius: 2px;
    }
}

.closeButtonWrapper {
    position: absolute;
    top: 0;
    left: 16px;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 38px;
    height: 38px;
    cursor: pointer;
    transform: translate3d(0, 0, 0);

    .closeButton {
        width: 22px;
        height: 22px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: transform 0.3s ease;

        svg {
            transform-origin: 50% 50%;
        }
    }

    &:hover .closeButton {
        transform: rotate(-90deg);
    }
}

.swiperContainer {
    position: relative;

    .preloader {
        width: 100%;
        padding-top: 65%;
        position: relative;

        > div {
            width: calc(100% - 32px);
            height: calc(100% - 32px);
            position: absolute;
            top: 0;
            left: 16px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: #ccc;
            border-radius: 12px;
        }
    }

    .swiper {
        width: 100%;
        overflow: hidden;

        .slide {
            width: 100%;
            overflow: hidden;
            padding: 0 16px;
            position: relative;

            .slideImage {
                width: 100%;
                padding-top: 100%;
                position: relative;

                .imagePreloader {
                    width: 100%;
                    height: 100%;
                    background-color: #333;
                    position: absolute;
                    top: 0;
                    left: 0;
                    border-radius: 12px;

                    > div {
                        display: flex;
                        width: 100%;
                        height: 100%;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }

                > img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    object-fit: cover;
                    display: none;
                    opacity: 0;
                    width: 100%;
                    height: 100%;
                    cursor: pointer;
                    border-radius: 12px;
                    transition: opacity 0.6s ease;

                    &.loaded {
                        display: block;
                        opacity: 1;
                    }
                }

                .zoomIcon {
                    width: 30px;
                    height: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    top: calc(50% - 15px);
                    left: calc(50% - 15px);
                    border-radius: 50%;
                    background-color: #f0f0f0;
                    color: #333;
                    transition: transform 0.3s ease, opacity 0.3s ease;
                    opacity: 0;
                    transform: scale(.9);
                    pointer-events: none;
                }
            }

            @media all and (min-width: $mobile-breakpoint) {
                &:hover .zoomIcon {
                    opacity: 1;
                    transform: none;
                }
            }
        }
    }

    .controlsContainer {
        width: 100%;
        display: flex;
        justify-content: center;
        position: absolute;
        bottom: 12px;
        left: 0;
        z-index: 1;
        padding: 0 12px;
        pointer-events: none;
        transform: translate3d(0, 0, 0);

        &.hidden {
            display: none;
        }

        .controls {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 2px 6px;
            border-radius: 100px;
            background-color: rgba(0,0,0,.5);
            pointer-events: auto;

            .control {
                cursor: pointer;
                transition: transform 0.3s ease;

                &:first-child:hover {
                    transform: translateX(-2px);
                }

                &:last-child:hover {
                    transform: translateX(2px);
                }

                > svg {
                    display: block;
                }

                @media all and (max-width: $mobile-breakpoint) {
                    display: none;
                }
            }

            .pagination {
                display: flex;
                align-items: center;

                .clickablePagination {
                    display: block;
                }

                .dot {
                    width: 16px;
                    height: 16px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                    background-color: transparent;
                    opacity: 1;

                    &.activeBullet {
                        display: flex;

                        > div {
                            opacity: 1;
                        }
                    }

                    &:not(.activeBullet):hover > div {
                        opacity: 0.6;
                    }

                    > div {
                        width: 8px;
                        height: 8px;
                        border-radius: 50%;
                        background-color: #fff;
                        opacity: 0.2;
                        transition: opacity 0.3s ease;
                    }
                }
            }
        }
    }
}

.content {
    padding: 16px 24px 24px 24px;

    *::selection {
        color: #fff;
        background-color: #333;
    }

    &.dark *::selection {
        color: #333;
        background-color: #fff;
    }

    .title {
        font-weight: 600;
        font-size: 22px;
        line-height: 1.3;
        margin: 0;
        user-select: text;
    }

    .price {
        display: block;
        font-weight: 500;
        font-size: 16px;
        margin: 6px 0 0 0;
        color: #fff;
        user-select: text;

        .lineThrough {
            font-weight: 400;
            font-size: 12px;
            text-decoration: line-through;
            opacity: 0.5;
            pointer-events: none;
            margin: 0 0 0 10px;
        }
    }

    .description {
        margin: 16px 0 0 0;
        white-space: pre-wrap;
        font-weight: 300;
        font-size: 16px;
        opacity: 0.95;
        word-break: break-word;
        user-select: text;
    }

    .externalButton {
        margin-top: 16px;
    }

    .buttonContent {
        width: 100%;
        user-select: none;

        > span {
            display: block;
            width: 100%;
            @include crop();
        }

        .url {
            font-weight: normal;
            font-size: 12px;
            line-height: 14px;
            opacity: 0.6;
        }
    }
}

.blurred {
    filter: blur(20px);
}

.contacts {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    padding: 8px 0 0 0;
}

.actionsBuy {
    padding: 0 16px 12px 16px;
}

.actionsBuyButton {
    margin: 0 !important;
}

.actionsWrap {
    margin: 16px 0;
}

.fixedPane {
    opacity: 0;
    pointer-events: none;

    > * {
        position: static;
        margin: 0 0 -24px 0;
    }
}

.noSwiping {
    display: inherit;
}

.cartBouncer {
    width: 48px;
    height: 48px;
    position: fixed;
    bottom: -60px;
    right: 16px;
    z-index: 10000000000;
    pointer-events: none;
}

@keyframes opacity {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
