@import 'vars';

.root {
    overflow: hidden;
    position: relative;
}

.list {
    width: 100%;
    display: flex;
    overflow: auto;
    list-style: none;
    margin: 0;
    padding: 0;

    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    scrollbar-width: none;

    &::before,
    &::after {
        content: '';
        display: block;
        padding: 0 8px;
    }
}

.clipLeft {
    mask-image: linear-gradient(90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px);
}

.clipRight {
    mask-image: linear-gradient(-90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px);
}

.clipLeft.clipRight {
    mask-composite: intersect;
    mask-image:
        linear-gradient(90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px),
        linear-gradient(-90deg, rgba(0,0,0,0) 30px, rgba(0,0,0,1) 50px);
}

.list::-webkit-scrollbar {
    width: 0;
    height: 0;
}

.list::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0);
}

.list::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0);
}

.controlContainerLeft,
.controlContainerRight {
    height: 100%;
    width: 44px;
    position: absolute;
    top: 0;
    z-index: 1;
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    user-select: none;
}

.control {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    transition: transform 0.3s ease;
}

.controlContainerLeft {
    left: 0;

    &:hover .control {
        transform: translateX(-3px);
    }
}

.controlContainerRight {
    right: 0;

    &:hover .control {
        transform: translateX(3px);
    }
}
