$color-white: #fff;
$color-black: #333;

.nav {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.step {
    position: relative;
    border-radius: 50%;
    border: 2px solid $color-black;
    width: 30px;
    height: 30px;
    color: $color-black;
    margin: 0 5px;

    &.done,
    &.current {
        background-color: $color-black;
        color: $color-white;
    }

    .icon,
    .mark {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .mark {
        position: absolute;
        top: 0;
    }

    &.done .icon {
        opacity: 0;
    }

    &.current {
        .icon {
            opacity: 1;
            transform: scale(0.9);
        }

        .mark {
            opacity: 0;
        }
    }
}

.divider {
    background-color: $color-black;

    &::after {
        display: block;
        content: '';
        top: 0;
        right: 0;
        height: 2px;
        background-color: inherit;
        width: 40px;
    }
}
