@import 'vars';

.root {
    width: calc(100% + 32px);
    margin: 0 0 10px -16px;
    font-size: 16px;
    font-weight: lighter;
}

.item {
    text-align: center;
    display: block;
    flex-grow: 1;
    margin: 0 8px 0 0;
    user-select: none;

    &:last-of-type {
        margin: 0;
    }

    > * {
        overflow: hidden;
        padding: 7px 11px;
        display: block;
        position: relative;
        white-space: nowrap;
        background-color: #fff;
        border-radius: 20px;
        color: #000;
        transition: box-shadow 0.3s ease, transform 0.3s ease;

        @media all and (min-width: $tablet-landscape-breakpoint) {
            &:hover {
                transform: translateY(-2px);
                box-shadow: 0 8px 8px -6px rgba(0, 0, 0, 0.24);
            }
        }

        &:active {
            transform: none;
        }
    }

    &.active {
        //transform: translateY(-1px);

        > * {
            @media all and (min-width: $tablet-landscape-breakpoint) {
                &:hover {
                    transform: none;
                    box-shadow: unset;
                }
            }
        }
    }
}

.list {
    font-weight: 500;
    font-size: 14px;
    padding: 6px 0;
}
