.pane {
    border-radius: 12px;
    padding: 10px 16px;
}

.row {
    display: flex;
    overflow: hidden;
}

.rowHeading {
    flex-shrink: 0;
    opacity: 0.8;
    overflow: hidden;
    padding: 0 10px 0 0;
}

.rowValue {
    flex-shrink: 1;
    text-align: right;
    align-self: center;
    font-weight: 500;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
}

.products .row {
    margin: 0 0 4px 0;

    &:last-of-type {
        margin: 10px 0 0 0;

        .rowHeading {
            font-weight: 600;
            align-self: flex-end;
        }

        .rowValue {
            font-weight: 900;
            font-size: 18px;
        }
    }

    .rowHeading {
        flex-shrink: 1;
        opacity: 1;
        overflow: hidden;
        padding: 0 10px 0 0;
        width: 100%;
    }

    .rowValue {
        flex-shrink: 0;
        text-align: right;
        align-self: flex-start;
        font-weight: 600;
        width: auto;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

.messengers {
    padding: 8px;

    & > div {
        margin: 0 !important;
        display: block;
        overflow: visible;
    }
}

.sectionHeading {
    margin: 0;
    padding: 10px 16px 5px 16px;
    font-weight: 600;
    font-size: 18px;
}

.buttons {
    margin-top: 16px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    gap: 8px;
    row-gap: 8px;
}

.paymentButtons > * {
    margin: 0 0 16px 0;

    &:last-child {
        margin: 0;
    }
}

.cancelPrompt {
    text-align: center;
    margin-bottom: 16px;
}
