.root {
    padding: 16px;

    > .icon {
        display: block;
        margin: 16px auto 8px auto;
    }

    .title {
        font-weight: 600;
        font-size: 28px;
        line-height: 1.2;
        margin: 16px 0;
        text-align: center;
    }

    .subTitle {
        text-align: center;
        margin: 0 0 16px 0;
    }

    .orderTitle {
        font-weight: 600;
        font-size: 18px;
        margin: 0 0 8px 0;
    }
}
