@import 'vars';
@import 'mixins/crop';

.root {
    margin-bottom: 16px;

    &:last-child,
    &:last-of-type {
        margin-bottom: 0;
    }

    @media all and (max-width: $mobile-breakpoint) {
        ul {
            padding: 0 0 0 10px;
            list-style-position: inside;

            li {
                margin: 0 0 5px 0;
                @include crop();
            }
        }
    }
}

.right {
    text-align: right;
}
