.root {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    padding: 0;
    margin: 6px 0 0;
    list-style-type: none;
    color: #dc5960;

    &.nonCritical {
        color: blue;
    }

    li {
        white-space: normal !important;
    }

    @keyframes shake {
        from,
        to {
            transform: translate3d(0, 0, 0);
        }

        10%, 30%, 50%, 70%, 90% {
            transform: translate3d(-5px, 0, 0);
        }

        20%, 40%, 60%, 80% {
            transform: translate3d(5px, 0, 0);
        }
    }

    &.shake {
        animation-name: shake;
    }

    &.animated {
        animation-duration: 1s;
        animation-fill-mode: both;
    }
}
