@import 'vars';

.root {
    flex: 1;
    display: flex;
    flex-direction: row;
    overflow: hidden;

    .content {
        flex: 1;
        display: flex;
        flex-direction: column;
        overflow: hidden;
    }

    .check {
        display: flex;
        align-items: center;
        margin-left: 16px;
        color: $placeholder-color;
    }

    &.active {
        .check {
            color: $link-color3;
        }
    }

    &.disabled {
        .check {
            color: $placeholder-color;
        }
    }
}
