@import 'vars';
@import 'mixins/crop';
@import 'mixins/border';

.root {
    position: relative;
    display: flex;

    .counter {
        opacity: 0;
        display: block;
        font-size: 12px;
        line-height: 18px;
        position: absolute;
        bottom: calc(50% - 10px);
        right: 12px;
        transform: scale(0.5);
        transition: transform 0.15s ease, opacity 0.3s ease;
    }

    .input {
        font-family: inherit;
        font-size: 16px;
        font-weight: normal;
        height: 42px;
        line-height: 24px;
        border: 1px solid #c7c7cc;
        border-radius: 4px;
        background-color: transparent;
        width: 100%;
        padding: 11px 16px;
        color: #2b303a;
        transition: border-color 0.3s ease, background-color 0.3s ease;
        @include crop();

        &:hover {
            border-color: #8e8e93;
        }

        &:focus {
            outline: 0;
            border-color: #007aff;

            + .counter {
                opacity: 1;
                transform: scale(1);
            }
        }

        &::placeholder {
            color: #8e8e93;
        }

        &.inlineEdit {
            border-color: transparent;

            &:hover {
                border-color: #8e8e93;
            }

            &:focus {
                border-color: #007aff;
            }
        }

        &.error {
            border-color: #dc5960;
        }

        &.small {
            height: 32px;
            font-size: 14px;
        }

        &.sizeNormal {
            padding: 6px;
            height: 32px;
            font-size: 14px;
            line-height: 20px;
        }

        &.grey {
            background-color: #f2f2f2;
            border: 1px solid #f2f2f2;
            height: 44px;
            line-height: 26px;
            padding: 11px 12px;

            &.error {
                border-color: #dc5960;
            }
        }

        &[type="date"]::-webkit-calendar-picker-indicator,
        &[type="time"]::-webkit-calendar-picker-indicator {
            opacity: 0;
            cursor: pointer;
        }

        &::-webkit-date-and-time-value {
            text-align: left;
        }
    }

    .customNativeIcon {
        display: block;
        position: absolute;
        width: 16px;
        height: auto;
        top: calc(50% - 8px);
        right: 16px;
        pointer-events: none;
    }

    &.disabled {
        .input {
            background-color: #eee;
            border: 1px solid transparent;
        }
    }

    .select {
        width: auto;
        border-radius: 4px 0 0 4px;
        border-right: none;
        transition: background-color 0.3s ease;

        &:hover,
        &:focus {
            background-color: #f5f5f5;
        }

        + .input-wrapper > .input {
            border-radius: 0 4px 4px 0;

            &.builderInput {
                border-radius: 0 10px 10px 0;
            }
        }
    }

    &.hexInput {
        .input,
        .contextBlock {
            height: 32px;
            padding: 11px;
        }

        .input {
            border-radius: 0 100px 100px 0;
        }

        .contextBlock {
            border-radius: 100px 0 0 100px;
            font-size: 14px;
        }

        .counter {
            display: none;
        }
    }

    .clearAddon {
        flex: 1;
        width: 42px;
        height: 42px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        position: absolute;
        top: 0;
        right: 0;
    }

    input[type='text'],
    input[type='email'],
    input[type='password'],
    input[type='tel'] {
        -webkit-appearance: none;
    }

    .input-wrapper {
        width: 100%;
        position: relative;
        display: flex;
    }

    &.icon {
        input {
            padding: 11px 16px 11px 56px;
        }

        > svg {
            position: absolute;
            left: 18px;
            top: 9px;
        }
    }

    .contextBlock {
        display: flex;
        align-items: center;
        font-size: 16px;
        font-weight: normal;
        height: 42px;
        line-height: 24px;
        color: #8e8e93;
        padding: 11px 16px;
        border: 1px solid #c7c7cc;
        background-color: #f5f5f5;
    }

    .root_grey {
        .contextBlock {
            border: 1px solid #333;
            background-color: #333;
            color: #fff;
            padding: 11px 12px;
            height: 44px;
            line-height: 26px;
        }
    }

    &.before {
        .contextBlock,
        .select {
            @include border-rr(0);
            border-right: none;

            + .input-wrapper > .input {
                @include border-lr(0);
            }
        }

        input {
            @include border-lr(0);
        }
    }

    &.after {
        .contextBlock,
        .select {
            @include border-lr(0);
            border-left: none;

            + .input-wrapper > .input {
                @include border-rr(0);
            }
        }

        input {
            @include border-rr(0);
        }
    }

    &.builderInput {
        .clearAddon {
            width: 38px;
            min-width: 38px;
            height: 38px;
            transition: background-color 0.3s ease;

            &.hasRightAddon {
                @include border-rr(8px);
            }

            &:focus,
            &:hover {
                background-color: #e5f6ff;
                color: #840008;
            }

            &.error {
                background-color: #fbeeef;
                color: #eaddde;
            }
        }

        .input {
            background-color: $background-light;
            border-radius: 10px;
            height: 38px;
            color: $font-default-color;
            border: none;
            transition: background-color 0.3s ease;

            &:focus,
            &:hover {
                background-color: #e5f6ff;

                .clearAddon {
                    background-color: #e5f6ff;
                }
            }

            &.error {
                background-color: #fbeeef;

                .clearAddon {
                    background-color: #fbeeef;
                }
            }
        }

        .contextBlock,
        .select {
            border: none;
            height: 38px;
            border-radius: 10px;
        }

        &.before {
            .contextBlock,
            .select {
                @include border-rr(0);

                + .input-wrapper > .input {
                    @include border-lr(0);
                }
            }

            input {
                @include border-lr(0);
            }
        }

        &.after {
            .contextBlock,
            .select {
                @include border-lr(0);

                + .input-wrapper > .input {
                    @include border-rr(0);
                }
            }

            input {
                @include border-rr(0);
            }
        }

        &.icon {
            input {
                padding: 11px 16px 11px 40px;
            }

            > svg {
                left: 9px;
                top: 7px;
                color: #b4b4bb !important;
                z-index: 1;
            }
        }

        .select {
            background-color: #e9e9e9;
            background-image: url('../../../../img/select_arrow.svg');
            background-position: right 10px center;
            background-repeat: no-repeat;
            padding: 0 24px 0 16px;
            color: #333;

            &:hover,
            &:focus {
                background-color: darken(#e9e9e9, 3%);
            }
        }

        &.disabled {
            .input {
                color: lighten($font-default-color, 15%);
                background-color: darken($background-light, 3%);
                border: none;
            }
        }
    }

    &.hidden {
        display: none;
    }

    .padding-right {
        padding-right: 46px;
    }

    &.clearable {
        .padding-right {
            padding-right: 72px;
        }

        .counter {
            margin-right: 25px;
        }
    }
}
