.root {
    display: flex;
    flex-direction: column;
}

.groupLabel {
    display: flex;
    flex-grow: 1;
    width: 100%;
    margin: 16px 0 8px 0;

    font-weight: bold;

    &:first-of-type {
        margin-top: 0;
    }
}

.choice {
    $color: #2b303a;
    display: flex;
    align-items: center;
    width: 100%;
    padding: 10px 0;
    cursor: pointer;
    text-decoration: none;
    color: $color;

    &:hover,
    &:focus {
        color: #007aff;
    }

    &.active {
        color: #007aff;
    }

    &.rounded {
        border-radius: 50%;
    }

    &>* {
        transition: color 0.3s ease;
    }
}

.icon,
.checkMark {
    display: flex;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    margin: 0 10px 0 0;

    > svg {
        transition: color 0.3s ease;
    }
}

.label {
    width: 100%;
    flex: 1;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.checkMark {
    margin: 0;

    svg {
        transform-origin: 50% 50%;
        opacity: 0;
        transform: scale(0.5);
        transition: transform 0.3s ease, opacity 0.3s ease, color 0.3s ease;
    }

    &.active > svg {
        opacity: 1;
        transform: none;
    }
}

.grid {
    &.root {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-around;
        margin: 0 -10px;
    }

    .choice {
        width: auto;
        padding: 2px;
        margin: 3px;
        border: 5px solid transparent;
        border-radius: 5px;

        &.rounded {
            border-radius: 50%;
            border: 1px solid #aaa;
            margin: 7px;
        }

        &.active {
            margin: 3px;
            background-color: #007aff;
            border: 5px solid #007aff;
            color: white;
        }
    }

    .label {
        display: flex;
    }

    .checkMark {
        display: none;
    }
}
