@import 'vars';
@import 'mixins/crop';

.root {
    display: flex;
    flex-direction: row;
    font-size: 14px;
    align-items: center;
    padding: 12px 0;
    position: relative;

    .divider {
        display: block;
        width: calc(100% - 86px);
        height: 1px;
        background-color: #cccccc;
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0.2;
    }

    &:first-of-type .divider {
        display: none;
    }

    .knob {
        opacity: 0.6;
        width: 16px;
        height: 16px;
        cursor: pointer;
        color: inherit;
        transition: opacity $default-transition;

        svg {
            color: inherit;
        }

        &:hover {
            opacity: 1;
        }
    }

    .thumbnail {
        width: 70px;
        height: 70px;
        position: relative;
        border-radius: 8px;
        overflow: hidden;
    }

    .meta {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-left: 16px;
        flex: 1;

        .title {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            font-weight: 500;

            > div {
                @include cropLines(2);
                margin: 0 0 5px 0;
            }
        }

        .details {
            font-weight: 300;
            font-size: 12px;
            padding-bottom: 8px;
        }

        .row {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
        }

        .quantity {
            display: flex;
            flex-direction: row;

            .quantityText {
                font-size: 14px;
                line-height: 14px;
            }
        }

        .price,
        .subTotal {
            font-weight: 600;
        }

        .subTotal {
            text-align: right;
        }
    }
}

.list {
    padding: 0;
    margin: 0;
}
